.button-swiper {
  position: absolute;
  top: 50%;
  width: 37px;
  height: 37px;
  z-index: 2;
  padding: 10px;
  cursor: pointer;
  --color-button: var(--color-swiper-button);

  @media (--small-vp) {
    width: 20px;
    height: 30px;
    padding: 0;
  }

  &::before {
    content: "";
